.sidebar {
    padding: 0 30px 0;
    @media (max-width: 756px) {
      display: none;
      }
    &__navigation-list {
      display: flex;
      flex-direction: column;
      gap: 20px;
      align-items: center;
      justify-content: center;
      width: 44px;
    }
    &__navigation-item--img:hover *{
      fill: #C83A2E;
    }
  }